@mixin font-size-default($size: 14) {
    $lineHeight: $size + 4;
    font-size: $size + px;
    line-height: $lineHeight + px;
}

@mixin mobile-block() {
    @media screen and (max-width: 768px) {
        width: 100%;
    }
}

@mixin multi-line ($num) {
    word-break: break-all;
    display: -webkit-box;
    -webkit-line-clamp: $num;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

@mixin single-line {
    overflow: hidden;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    -webkit-text-overflow: ellipsis;
    -moz-text-overflow: ellipsis;
    white-space: nowrap;
}
.container {
  margin: 32px;
  padding: 24px;
  text-align: center;
  background-color: #fff;
  height: 100%;
}

.main {
  gap: 12px;
  display: flex;
  flex-direction: row;
  max-width: 1200px;
  margin: auto;
}

.actions {
  margin-top: 32px;
  gap: 12px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}